import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Media from 'react-media';
import style from './newsIndex.module.css';
import newsList from '../../bd/newsList.json';
import NewsListMob from './newsListMob/newsListMob';
import NewsListDesk from './newsListDesk/newsListDesk';

const NewsContainer = ({ header, button, currentLocale }) => (
  <div className="wrapper">
    <div className={style.wrapper}>
      <h3 className={style.newsHeader}>{header}</h3>
      <Media query={{ maxWidth: 1199 }}>
        {matches =>
          matches ? (
            <NewsListMob
              news={currentLocale === 'ru' ? newsList.newsRu : newsList.newsUk}
            />
          ) : (
            <NewsListDesk
              news={currentLocale === 'ru' ? newsList.newsRu : newsList.newsUk}
            />
          )
        }
      </Media>
      <Link className={style.button} to="/news">
        {button}
      </Link>
    </div>
  </div>
);

export default NewsContainer;
