import React from 'react';
import Media from 'react-media';
import { ReactSvgInjector, Mutate } from 'react-svg-injector';
import { Link } from 'react-router-dom';
import style from './whitePart.module.css';
import logoUk from '../../../assets/img/logo/logo_uk.png';
import logoRu from '../../../assets/img/logo/logo_ru.png';
import logoUkW from '../../../assets/img/logo/logo_uk.webp';
import logoRuW from '../../../assets/img/logo/logo_ru.webp';
import logoUkJ from '../../../assets/img/logo/logo_uk.jp2';
import logoRuJ from '../../../assets/img/logo/logo_ru.jp2';
import telegram from '../../../assets/img/icons/telegram.svg';
import viber from '../../../assets/img/icons/viber.svg';
import facebook from '../../../assets/img/icons/facebook.svg';

const WhitePart = ({ callBack, currentLocale }) => (
  <div className={style.wrapper}>
    <Link to="/">
      <picture>
        <source
          srcSet={currentLocale === 'ru' ? logoRuW : logoUkW}
          type="image/webp"
        />
        <source
          srcSet={currentLocale === 'ru' ? logoRuJ : logoUkJ}
          type="image/jp2"
        />
        <source
          srcSet={currentLocale === 'ru' ? logoRu : logoUk}
          type="image/png"
        />
        <img
          className={style.img}
          src={currentLocale === 'ru' ? logoRu : logoUk}
          alt="infox logo"
        />
      </picture>
    </Link>
    <div className={style.textWrapper}>
      <Media query={{ minWidth: 1200 }}>
        {matches => matches && <p className={style.callBack}>{callBack}</p>}
      </Media>
      <div className={style.socials}>
        <Media query={{ maxWidth: 1199 }}>
          {matches => matches && <div className={style.line} />}
        </Media>
        <a className={style.linkControl} href="#">
          <ReactSvgInjector
            src={telegram}
            alt="telegram icon"
            className={style.imgControl}
          >
            <Mutate selector="g" />
          </ReactSvgInjector>
        </a>
        <a className={style.linkControl} href="#">
          <ReactSvgInjector
            src={viber}
            alt="viber icon"
            className={style.imgControl}
          >
            <Mutate selector="g" />
          </ReactSvgInjector>
        </a>
        <a
          className={style.linkControl}
          target="_blank"
          rel="noopener noreferrer"
          href="https://www.facebook.com/infoxvodokanal"
        >
          <ReactSvgInjector
            src={facebook}
            alt="facebook icon"
            className={style.imgControl}
          >
            <Mutate selector="g" />
          </ReactSvgInjector>
        </a>
        <Media query={{ maxWidth: 1199 }}>
          {matches => matches && <div className={style.line} />}
        </Media>
      </div>
    </div>
  </div>
);

export default WhitePart;
