import React from 'react';
import { Link } from 'react-router-dom';
import style from './newsListMob.module.css';

const NewsListMob = ({ news = [] }) => (
  <ul className={style.list}>
    {news.map((item, i) => {
      if (i < 4) {
        return (
          <li className={style.listItem} key={item.id}>
            <picture>
              <source srcSet={item.imgW} type="image/webp" />
              <source srcSet={item.imgJ} type="image/jp2" />
              <source srcSet={item.img} type="image/jpg" />
              <img className={style.imgBack} src={item.img} alt="news img" />
            </picture>
            <Link className={style.link} to={`/news/${item.id}`} />
            <div className={style.date}>{item.date}</div>
            <p className={style.newsHead}>{item.title}</p>
          </li>
        );
      }
    })}
  </ul>
);

export default NewsListMob;
