import React from 'react';
import style from './footer.module.css';
import WhitePart from './whitePart/whitePart';
import DarkPart from './darkPart/darkPart';

const Footer = ({ footer, currentLocale }) => (
  <footer>
    <div className={style.backWhite}>
      <div className="wrapper">
        <WhitePart callBack={footer.callBack} currentLocale={currentLocale} />
      </div>
    </div>
    <div className={style.backDark}>
      <div className="wrapper">
        <DarkPart {...footer} />
      </div>
    </div>
  </footer>
);

export default Footer;
